input {
  font-family: $font-family-serif;
  color: $aqua-dark;
}

label {
  @extend h6;
}

.comment-form {
  textarea {
    background: white;
    border: 1px solid $navy;
  }

  .form-submit {
    margin: 0;
  }

  .submit {
    width: auto;
    background: $orange-med;
    box-shadow: none !important;
    border-radius: 0;
    font-family: $font-comic;
    text-shadow: 4px 2px $orange-dark;
    @extend .btn;

    &:hover, &:focus, &:active {
      background: $orange-dark;
    }
  }
}

// Submission form styles
.wpcf7-form {
  input[type="text"] {
    margin-bottom: 0;
    font-size: 25px;
  }

  fieldset {
    position: relative;
    border: 1px solid $navy;
    padding: 10px 10px 0;
    margin-bottom: 20px;
    background: $ice;
  }

  .row {
    position: relative;
    margin: 0 0 10px;
    border: 1px solid $navy;
    background: white;
    padding: 20px;
  }

  .wpcf7-form-control-wrap {
    display: block;
    position: relative;

    &.photo {
      position: static;
    }

    &.choice {
      margin-bottom: 1em;

      .wpcf7-list-item, [type="radio"] + .wpcf7-list-item-label {
        display: block;
        line-height: 1.5em;
      }

      & ~ div[id*="-wrap"] {
        height: 0;
        overflow: hidden;

        &.active {
          height: auto;
        }

        select {
          display: block;
        }
      }
    }
  }

  .wpcf7-textarea {
    @extend .materialize-textarea;
  }

  .file-field {
    .btn {
      background: $aqua;
      box-shadow: none !important;
      border-radius: 0;
      font-family: $font-comic;
      font-size: 25px;
      text-shadow: 4px 2px $aqua-dark;
    }
  }

  .input-field.inline {
    width: 100%;

    input {
      display: inline-block;
      width: 50%;
      margin: 0;

      &[type="button"] {
        width: auto;
        background: $orange-med;
        box-shadow: none !important;
        border-radius: 0;
        font-family: $font-comic;
        text-shadow: 4px 2px $orange-dark;
        @extend .btn;

        &:hover, &:focus, &:active {
          background: $orange-dark;
        }
      }
    }
  }

  // Radio styles from Materialize
  [type="radio"]:not(:checked) + .wpcf7-list-item-label,
  [type="radio"]:checked + .wpcf7-list-item-label {
    position: relative;
    padding-left: 35px;
    cursor: pointer;
    display: inline-block;
    height: 25px;
    line-height: 25px;
    font-size: 1rem;
    transition: 0.28s ease;
    user-select: none;
  }

  [type="radio"] + .wpcf7-list-item-label::before,
  [type="radio"] + .wpcf7-list-item-label::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    margin: 4px;
    width: 16px;
    height: 16px;
    z-index: 0;
    transition: 0.28s ease;
  }

  /* Unchecked styles */
  [type="radio"]:not(:checked) + .wpcf7-list-item-label::before,
  [type="radio"]:not(:checked) + .wpcf7-list-item-label::after,
  [type="radio"]:checked + .wpcf7-list-item-label::before,
  [type="radio"]:checked + .wpcf7-list-item-label::after,
  [type="radio"].with-gap:checked + .wpcf7-list-item-label::before,
  [type="radio"].with-gap:checked + .wpcf7-list-item-label::after {
    border-radius: 50%;
  }

  [type="radio"]:not(:checked) + .wpcf7-list-item-label::before,
  [type="radio"]:not(:checked) + .wpcf7-list-item-label::after {
    border: 2px solid $radio-empty-color;
  }

  [type="radio"]:not(:checked) + .wpcf7-list-item-label::after {
    transform: scale(0);
  }

  /* Checked styles */
  [type="radio"]:checked + .wpcf7-list-item-label::before {
    border: 2px solid transparent;
  }

  [type="radio"]:checked + .wpcf7-list-item-label::after,
  [type="radio"].with-gap:checked + .wpcf7-list-item-label::before,
  [type="radio"].with-gap:checked + .wpcf7-list-item-label::after {
    border: $radio-border;
  }

  [type="radio"]:checked + .wpcf7-list-item-label::after,
  [type="radio"].with-gap:checked + .wpcf7-list-item-label::after {
    background-color: $radio-fill-color;
  }

  [type="radio"]:checked + .wpcf7-list-item-label::after {
    transform: scale(1.02);
  }

  /* Radio With gap */
  [type="radio"].with-gap:checked + .wpcf7-list-item-label::after {
    transform: scale(0.5);
  }

  /* Focused styles */
  [type="radio"].tabbed:focus + .wpcf7-list-item-label::before {
    box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.1);
  }

  /* Disabled Radio With gap */
  [type="radio"].with-gap:disabled:checked + .wpcf7-list-item-label::before {
    border: 2px solid $input-disabled-color;
  }

  [type="radio"].with-gap:disabled:checked + .wpcf7-list-item-label::after {
    border: none;
    background-color: $input-disabled-color;
  }

  /* Disabled style */
  [type="radio"]:disabled:not(:checked) + .wpcf7-list-item-label::before,
  [type="radio"]:disabled:checked + .wpcf7-list-item-label::before {
    background-color: transparent;
    border-color: $input-disabled-color;
  }

  [type="radio"]:disabled + .wpcf7-list-item-label {
    color: $input-disabled-color;
  }

  [type="radio"]:disabled:not(:checked) + .wpcf7-list-item-label::before {
    border-color: $input-disabled-color;
  }

  [type="radio"]:disabled:checked + .wpcf7-list-item-label::after {
    background-color: $input-disabled-color;
    border-color: $input-disabled-solid-color;
  }
}

// Hidden input fields
.wpcf7-latlong, input[name="datetime"] {
  display: none;
}

.flatpickr-calendar {
  border-radius: 0;

  .numInputWrapper {
    &:hover {
      background: rgba($shadow, 0.1);
    }
  }

  input.numInput {
    font-size: inherit;
  }

  .flatpickr-day {
    &.selected {
      background: $aqua;
      border-color: $aqua-dark;
    }

    &:hover {
      background: rgba($shadow, 0.1);
    }
  }

  .flatpickr-time {
    height: auto !important;
    max-height: none;

    .numInputWrapper {
      height: auto;
    }
  }
}

div[id="map_canvas"] {
  width: 100% !important;

  .gm-style-pbt {
    text-align: center;
  }
}



